import {customElement, BaseElement, closestElement, debounce, getCSSVariable} from '@here/hds-base';
import '@here/hds-button';
import {HDSGrid} from '@here/hds-grid';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import '@here/hds-overlay';
import {html, LitElement, nothing, PropertyValues, TemplateResult} from 'lit';
import {property, query, queryAll} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styleMap} from 'lit/directives/style-map.js';
import {tabbable} from 'tabbable';
import {styles} from './hds-modal.css.js';
import {getHDSTabblableComponents} from './hds-tabbable.js';

export type ModalTitleSize = 'small' | 'large';
export type ModalVariant = 'primary' | 'secondary' | 'subtle' | 'warning' | 'danger' | 'link';
export type ModalChangePayload = {from: 'button' | 'overlay' | 'escape'};
export type ModalActionPayload = {from: 'confirm' | 'cancel' | 'thirdButton'};

/**
 * @slot header Content to be shown as title on the header
 * @slot body Main content of the modal
 * @slot footer Content to be shown on the footer
 *
 * @event {CustomEvent<{from: 'button' | 'overlay' | 'escape'}>} change Fired when the modal is changed (closed/open) and from where has been closed: `button | overlay | escape` `ModalChangePayload`
 * @event {CustomEvent<{from: 'confirm' | 'cancel' | 'thirdButton'}>} action Fired when one of the actions button has been clicked `ModalActionPayload`
 *
 * @cssprop --hds-modal-tablet-grid-column Column property value for the modal width for `tablet` breakpoint
 * @cssprop --hds-modal-desktop-small-grid-column Column property value for the modal width for `desktop-small` breakpoint
 * @cssprop --hds-modal-desktop-medium-grid-column Column property value for the modal width for `desktop-medium` breakpoint
 * @cssprop --hds-modal-desktop-large-grid-column Column property value for the modal width for `desktop-large` breakpoint
 * @cssprop --hds-modal-body-height Variable for the content height (default `100%`)
 */
@customElement('hds-modal')
export class Modal extends BaseElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: false};
  @property({type: Boolean, reflect: true, attribute: 'hide-close-btn'}) hideCloseBtn = false;
  @property({type: String, reflect: true, attribute: 'close-aria-label'}) closeAriaLabel = 'Close';
  @property({type: String}) icon = '';
  @property({type: String, reflect: true, attribute: 'icon-color'}) iconColor = '';
  @property({type: String, reflect: true, attribute: 'icon-category'}) iconCategory: IconCategory =
    'core-ui';
  @property({type: String, reflect: true, attribute: 'heading-message'}) headingMessage = '';
  @property({type: String}) message = '';
  @property({type: String, reflect: true, attribute: 'title-size'}) titleSize: ModalTitleSize =
    'large';
  @property({type: Boolean, attribute: 'disable-close-on-overlay-click'})
  disableCloseOnOverlayClick = false;
  @property({type: Boolean, attribute: 'disable-close-on-escape'}) disableCloseOnEscape = false;
  @property({type: Boolean, attribute: 'hide-header'}) hideHeader = false;
  @property({type: Boolean, attribute: 'hide-footer'}) hideFooter = false;
  @property({type: String, reflect: true, attribute: 'confirm-variant'})
  confirmVariant: ModalVariant = 'primary';
  @property({type: Number, reflect: true, attribute: 'buttons-width'}) buttonsWidth = 128;
  @property({type: Boolean, attribute: 'hide-confirm'}) hideConfirm = false;
  @property({type: Boolean, attribute: 'hide-cancel'}) hideCancel = false;
  @property({type: Boolean, attribute: 'hide-third-button'}) hideThirdButton = false;
  @property({type: String, reflect: true, attribute: 'confirm-text'}) confirmText = 'Done';
  @property({type: String, reflect: true, attribute: 'cancel-text'}) cancelText = 'Cancel';
  @property({type: String, reflect: true, attribute: 'third-button-text'}) thirdButtonText =
    'Settings';
  protected tabbableElements: any[] = [];
  protected debouncedResize = debounce(() => this.changeBodyMaxHeight(), 150);
  protected HDS_SPACING_10 = 64;
  @query('slot[name="header"]') protected headerSlot!: HTMLSlotElement;
  @query('slot[name="body"]') protected bodySlot!: HTMLSlotElement;
  @query('slot[name="footer"]') protected footerSlot!: HTMLSlotElement;
  @query('.close') protected closeBtn!: HTMLElement;
  @queryAll('.hds-modal-footer--default .default-button') protected defaultButtons!: HTMLElement[];
  protected debouncedButtonResize = debounce(() => this.changeDefaultButtonSize(), 150);
  // Holds the active element before the modal is opened.
  private activeElement: Element | null = null;

  constructor() {
    super();
    this.handleKeydown = this.handleKeydown.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
  }

  protected get headerSlotted() {
    return !!this.slotNodes(this.headerSlot).length;
  }

  private get firstTabbableElement() {
    return this.tabbableElements[0];
  }

  private get lastTabbableElement() {
    return this.tabbableElements[
      this.tabbableElements.length ? this.tabbableElements.length - 1 : 0
    ];
  }

  connectedCallback() {
    super.connectedCallback();
    this.addEventListeners();
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.removeEventListeners();
    this.removeHandleFocusEvents();
    this.activeElement = null;
  }

  firstUpdated(changedProperties: PropertyValues) {
    super.firstUpdated(changedProperties);
    const spacing = parseInt(getCSSVariable('--hds-spacing-10', this), 10);
    const baseline = parseInt(getCSSVariable('--hds-font-baseline', this), 10);
    if (spacing && baseline) {
      this.HDS_SPACING_10 = spacing * baseline;
    }
    const parentEl = closestElement<HTMLElement>("[data-theme^='hds-']", this);
    const container = document.querySelector('hds-modal-container');
    if (container) {
      container.appendChild(this);
    } else if (parentEl) {
      parentEl.appendChild(this);
    } else {
      // eslint-disable-next-line no-console
      console.error(
        "No parent element with data attribute [data-theme^='hds-'] found. Data attribute with name of existing hds theme should be added to the root container."
      );
    }
  }

  protected async updated() {
    this.setTabbableElements();
    await this.changeBodyMaxHeight();
    await this.changeDefaultButtonSize();
  }

  /**
   * Adds focus trap on modal window to prevent keyboard navigation outside of modal
   */
  protected setTabbableElements() {
    const bodyElements = this.slotNodes(this.bodySlot);
    const bodyNode = bodyElements[0] as Element;

    const footerElements = this.slotNodes(this.footerSlot);
    const footerNode = footerElements[0] as Element;

    // Web components tabbable elements
    const tabbableFooterComponents = getHDSTabblableComponents(footerNode);
    const tabbableBodyComponents = getHDSTabblableComponents(bodyNode);

    this.tabbableElements = [
      ...(bodyElements.length ? tabbable(bodyElements[0] as Element) : []),
      ...tabbableBodyComponents,
      ...(footerElements.length ? tabbable(footerElements[0] as Element) : []),
      ...tabbableFooterComponents,
    ];
    if (!this.hideCloseBtn && this.headerSlotted) {
      this.tabbableElements.push(this.closeBtn);
    }
    this.setTabKeydownListeners();
  }

  protected setTabKeydownListeners() {
    if (this.tabbableElements.length && this.tabbableElements[0]?.focus) {
      this.focusFirstTabbableElement();
      this.tabbableElements.forEach(element =>
        element?.addEventListener('keydown', this.handleFocus)
      );
    }
  }

  protected render() {
    return html`
      <div class="-hds-modal-wrapper hds-grid-container">
        <div class="wrapper" role="dialog" aria-modal="true" aria-label="${ifDefined(this.title)}">
          ${this.renderHeader()}
          <div
            class="hds-modal-body"
            id="content"
            @scroll="${() =>
              this.dispatchEvent(new CustomEvent('scroll', {composed: true, bubbles: true}))}"
          >
            <slot name="body"></slot>
            <slot>
              <div>${this.message}</div>
            </slot>
          </div>
          ${this.renderFooter()} ${this.renderCloseButton()}
        </div>
        <hds-overlay @click="${this.handleClickOverlay}"></hds-overlay>
      </div>
    `;
  }

  protected renderHeader(): TemplateResult | unknown {
    if (this.hideHeader) {
      return nothing;
    }

    const classes = {
      'hds-modal-header': true,
      '-icon': this.icon,
    };

    const titleClasses = {
      title: true,
      [`title--size-${this.titleSize}`]: true,
    };

    let iconElement = html``;
    let localStyles = {};
    if (this.iconColor || this.iconColor !== '') {
      localStyles = {
        '--hds-icon-color': this.wrapCssVariable(this.iconColor),
      };
    }

    if (this.icon) {
      iconElement = html` <hds-icon
        class="title-icon"
        name="${this.icon}"
        category="${this.iconCategory}"
      ></hds-icon>`;
    }

    if (!this.icon && !this.headerSlotted && !this.headingMessage) {
      return html` <div class="${classMap(classes)}">
        <slot name="header"></slot>
      </div>`;
    }

    return html`
      <div id="modal-header" class="${classMap(classes)}" style="${styleMap(localStyles)}">
        ${iconElement}
        <div id="title" class="${classMap(titleClasses)}">
          <slot name="header">${this.headingMessage}</slot>
        </div>
      </div>
    `;
  }

  protected removeHandleFocusEvents() {
    this.tabbableElements.forEach(element =>
      element?.removeEventListener('keydown', this.handleFocus)
    );
  }

  /**
   * Close action on Overlay click key down
   */
  protected handleClickOverlay() {
    if (!this.disableCloseOnOverlayClick) {
      this.close('overlay');
    }
  }

  protected renderFooter(): TemplateResult | unknown {
    if (this.hideFooter) {
      return nothing;
    }

    return html`
      <div id="modal-footer" class="hds-modal-footer">
        <slot name="footer">${this.renderButtons()}</slot>
      </div>
    `;
  }

  protected renderCloseButton() {
    if (this.hideHeader) {
      return nothing;
    }

    const heading = this.shadowRoot?.querySelector('#modal-header');
    if (!this.hideCloseBtn) {
      heading?.classList.add('with-close');
      return html`
        <hds-button
          class="close"
          icon="cross"
          variant="subtle-quiet"
          @click="${() => this.close('button')}"
          aria-label="${this.closeAriaLabel}"
          size="small"
        ></hds-button>
      `;
    }
    heading?.classList.remove('with-close');
    return nothing;
  }

  /**
   * Set large size to default buttons in footer on small screens
   */
  protected async changeDefaultButtonSize(breakpointName = 'desktop-small') {
    const documentWidth = this.shadowRoot!.ownerDocument.body.offsetWidth;
    this.defaultButtons.forEach(button => {
      const breakpointWidth: number =
        HDSGrid.getWidthForBreakpoint(breakpointName, this) &&
        typeof HDSGrid.getWidthForBreakpoint(breakpointName, this) === 'number'
          ? (HDSGrid.getWidthForBreakpoint(breakpointName, this) as number)
          : 0;
      documentWidth <= breakpointWidth
        ? button.setAttribute('size', 'large')
        : button.setAttribute('size', 'small');
    });
  }

  /**
   * Set calculated footer and header height as custom css variable
   * for body max height calculation in css for calc function
   */
  protected async changeBodyMaxHeight() {
    await this.updateComplete;
    const body = this.shadowRoot!.querySelector('#content') as HTMLElement;
    const header = this.shadowRoot!.querySelector('#modal-header') as HTMLElement;
    const footer = this.shadowRoot!.querySelector('#modal-footer') as HTMLElement;
    const viewPortHeight = window.innerHeight;

    const headerHeight = header?.offsetHeight || 0;
    const footerHeight = footer?.offsetHeight || 0;
    const elementsHeight = `${
      viewPortHeight - (headerHeight + footerHeight + this.HDS_SPACING_10)
    }px`;

    body.style.setProperty('--hds-modal-max-height', elementsHeight);
  }

  /**
   * Return array of nodes in particular slot or empty array
   * @param slot
   * @returns All the Elements in the slots
   */
  protected slotNodes(slot: HTMLSlotElement) {
    return slot?.assignedElements({flatten: true}) || [];
  }

  /**
   * Wraps css variable to be placed in styles
   */
  protected wrapCssVariable = (variableName: string | undefined) =>
    variableName?.startsWith('--') ? `var(${variableName})` : variableName;

  /**
   * Focus first element
   */
  private focusFirstTabbableElement() {
    setTimeout(() => this.firstTabbableElement.focus());
  }

  /**
   * Focus previously stored active element after close modal
   */
  private focusActiveElement(): void {
    if (this.activeElement && (this.activeElement as HTMLElement)?.focus) {
      (this.activeElement as HTMLElement).focus();
    }
  }

  private handleFocus(e: KeyboardEvent) {
    if (e.code !== 'Tab') {
      return;
    }
    if (e.shiftKey && e.target === this.firstTabbableElement && this.lastTabbableElement?.focus) {
      e.preventDefault();
      this.lastTabbableElement?.focus();
    } else if (
      !e.shiftKey &&
      e.target === this.lastTabbableElement &&
      this.firstTabbableElement?.focus
    ) {
      e.preventDefault();
      this.firstTabbableElement?.focus();
    }
  }

  private renderButtons(): TemplateResult {
    const confirm = this.hideConfirm
      ? nothing
      : this.renderDefaultButton(this.confirmVariant, this.confirmText, 'confirm');
    const cancel = this.hideCancel
      ? nothing
      : this.renderDefaultButton('secondary', this.cancelText, 'cancel');
    const thirdButton = this.hideThirdButton
      ? nothing
      : this.renderDefaultButton('subtle', this.thirdButtonText, 'thirdButton');

    return html` <div class="hds-modal-footer--default">${thirdButton}${cancel}${confirm}</div>`;
  }

  private buttonClick(name: 'confirm' | 'cancel' | 'thirdButton') {
    this.dispatchEvent(
      new CustomEvent<ModalActionPayload>('action', {
        detail: {
          from: name,
        },
      })
    );
  }

  private close(value: 'button' | 'overlay' | 'escape'): void {
    this.dispatchEvent(
      new CustomEvent<ModalChangePayload>('change', {
        detail: {
          from: value,
        },
      })
    );
    this.focusActiveElement();
  }

  private renderDefaultButton(
    variant: ModalVariant,
    text: string,
    name: 'confirm' | 'cancel' | 'thirdButton'
  ): TemplateResult {
    return html` <hds-button
      variant="${variant}"
      class="default-button ${name}"
      width="100%"
      @click="${() => this.buttonClick(name)}"
      aria-label="${text}"
      size="small"
      >${text}
    </hds-button>`;
  }

  private addEventListeners() {
    this.removeEventListeners();
    this.ownerDocument.addEventListener('keydown', this.handleKeydown);
    window.addEventListener('resize', this.debouncedResize);
    window.addEventListener('resize', this.debouncedButtonResize);
  }

  private removeEventListeners() {
    this.ownerDocument.removeEventListener('keydown', this.handleKeydown);
    window.removeEventListener('resize', this.debouncedResize);
    window.removeEventListener('resize', this.debouncedButtonResize);
    this.removeHandleFocusEvents();
  }

  /**
   * Close action on Escape key down
   */
  private handleKeydown(e: KeyboardEvent) {
    if (e.key === 'Escape' && !this.disableCloseOnEscape) {
      this.close('escape');
    }
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-modal': Modal;
  }
}
